import React, { Component } from 'react';
import { Row, Form, Col } from 'antd';
import { getTrainTypes } from 'services/trainType';
import { SelectAjax } from 'components/common';
import { requiredRule } from 'utils/validateRules';
import { BtnSearch, BtnClear } from 'components/common/button';
import { getResgisterSessionSelect } from 'services/registerSession';
import { getFacultyByRegisterSession } from 'services/gradePartGrading';
import { getSubjectByFacultyAndSession } from 'services/classCredit';
import { getClassCreditForSelect } from 'services/classCreditSchedule';

import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};

  componentWillReceiveProps(nextProps) {
    if (nextProps.reload !== this.props.reload) {
      this.handleSearch(null, nextProps.selectedGradeParts);
    }
  }

  getResgisterSessionSelectCustom = async params => {
    const res = await getResgisterSessionSelect({ ...params, fullFormat: true });
    return res;
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = (e, selectedGradeParts) => {
    if (e) {
      e.preventDefault();
    }
    this.props.form.validateFields(async (err, { classCreditId }) => {
      if (!err) {
        this.props.handleSearch({ id: classCreditId, gradeParts: selectedGradeParts || [] });
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldValue, setFieldsValue } = this.props.form;
    const trainTypeId = getFieldValue('trainTypeId');
    const registerSessionId = getFieldValue('registerSessionId');
    const facultyId = getFieldValue('facultyId');
    const subjectId = getFieldValue('subjectId');

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          {/* <FilterPathEducate form={this.props.form} /> */}
          <Col span={6}>
            <Form.Item label="H">
              {getFieldDecorator('trainTypeId', {
                rules: [requiredRule],
              })(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col span={10}>
            <Form.Item label="t ng k">
              {getFieldDecorator('registerSessionId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={this.getResgisterSessionSelectCustom}
                  params={{ trainTypeId, fullformat: true }}
                  allowGetData={!!trainTypeId}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={7}>
            <Form.Item label="Khoa">
              {getFieldDecorator('facultyId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultyByRegisterSession}
                  params={{ registerSessionId }}
                  allowGetData={!!registerSessionId}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Hc phn">
              {getFieldDecorator('subjectId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSubjectByFacultyAndSession}
                  params={{ registerSessionId, facultyId }}
                  allowGetData={!!registerSessionId && !!facultyId}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Lp tn ch">
              {getFieldDecorator('classCreditId', {
                rules: [requiredRule],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getClassCreditForSelect}
                  params={{ registerSessionId, facultyId, subjectId }}
                  allowGetData={!!registerSessionId && !!facultyId && !!subjectId}
                  keyValue="value"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



